.nav {
  & svg {
    fill: var(--secondary);
  }
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.item {
  &:not(:last-child) {
    margin-right: 30px;
  }
}

.link {
  &:hover {
    transition: opacity 0.3s;
    opacity: 0.6;
  }
}
